import React from "react"
import "./css/topimage.css"
import DeskTopDarkImg from "./images/bg-desktop-dark.jpg"
import DeskTopLightImg from "./images/bg-desktop-light.jpg"
import { Fade } from "@mui/material"

function TopImage({ icon, setIcon }) {
  return (
    <Fade in={true}>
      <div
        className="top-image"
        style={{
          background: `url(${icon ? DeskTopDarkImg : DeskTopLightImg})`
        }}
      ></div>
    </Fade>
  )
}

export default TopImage
