import React, { useEffect, useState } from "react"
import WbSunnyIcon from "@mui/icons-material/WbSunny"
import ListItem from "./ListItem"
import NightlightRoundIcon from "@mui/icons-material/NightlightRound"
import "./css/listcontainer.css"
import Empty from "./Empty"
import { Zoom } from "@mui/material"

function ListContainer({ icon, setIcon }) {
  const [item, setItem] = useState({ item: "", checked: false })
  const [items, setItems] = useState([])
  const [left, setLeft] = useState(0)

  // for active
  const [state, setState] = useState({
    All: true,
    Active: false,
    Completed: false
  })

  function handleChange(e) {
    const { value } = e.target
    setItem((prev) => {
      return { ...prev, item: value }
    })
  }
  function handleClick(e) {
    const key = e.key
    if (key === "Enter") {
      setItems((prev) => {
        return [...prev, item]
      })
      setItem({ item: "", checked: false })
      setState({ All: true, Active: false, Completed: false })
      setLeft((prev) => {
        return prev + 1
      })
    }
  }

  async function handleCheck(id) {
    await setItems((prev) => {
      return prev.map((element, index) => {
        if (id === index) {
          if (element.checked === true) {
            setLeft((prev) => {
              return prev + 1
            })
          } else if (element.checked === false) {
            setLeft((prev) => {
              return prev - 1
            })
          }
          return { ...element, checked: !element.checked }
        } else {
          return element
        }
      })
    })
  }

  function deleteItem(id) {
    console.log("id is " + id)
    setItems((prev) => {
      return prev.filter((element, index) => {
        if (id === index) {
          if (element.checked === false) {
            setLeft((prev) => {
              return prev - 1
            })
          }
        }
        return id !== index
      })
    })
  }
  function handleState(e) {
    const name = e.target.textContent
    if (name === "Completed") {
      setState({ All: false, Active: false, Completed: true })
    } else if (name === "All") {
      setState({ All: true, Active: false, Completed: false })
    } else if (name === "Active") {
      setState({ All: false, Active: true, Completed: false })
    }
  }

  async function deleteCompleted(e) {
    e.preventDefault()
    setItems((prev) => {
      return prev.filter(({ checked }) => {
        return checked !== true
      })
    })
    setState({ All: true, Active: false, Completed: false })
  }

  useEffect(() => {
    const list = JSON.parse(window.localStorage.getItem("list"))
    setLeft(list.length)
    list &&
      list.forEach((element) => {
        if (element.checked === true) {
          setLeft((prev) => {
            return prev - 1
          })
        }
      })

    if (list) {
      console.log("there is a list...")
      setItems(list)
    }
  }, [])

  useEffect(() => {
    window.localStorage.setItem("list", JSON.stringify(items))
  }, [items])

  return (
    <div className="list-container">
      {/* header for list container...... */}
      <div className="list-container-header">
        <h2>TODO</h2>
        {icon && (
          <WbSunnyIcon
            style={{ color: "white", cursor: "pointer" }}
            onClick={() => {
              setIcon(false)
            }}
          />
        )}
        {!icon && (
          <NightlightRoundIcon
            style={{
              color: "white",
              transform: "rotate(-45deg)",
              fontSize: "1.7rem",
              cursor: "pointer"
            }}
            onClick={() => {
              setIcon(true)
            }}
          />
        )}
      </div>

      {/* input area to add item in the list */}
      <div className="add-item-container">
        <div></div>
        <input
          type="text"
          placeholder="Create a New Todo..."
          value={item.item}
          name="item"
          onChange={handleChange}
          onKeyPress={handleClick}
          autoFocus
        />
      </div>

      {/* list item container */}
      <div className="list-item-container">
        {items.length === 0 && <Empty />}
        {items.length > 0 && (
          <ul>
            {items.map(({ item, checked }, index) => {
              if (state.All) {
                return (
                  <ListItem
                    key={index}
                    id={index}
                    name={item}
                    checked={checked}
                    handleCheck={handleCheck}
                    deleteItem={deleteItem}
                  />
                )
              } else if (state.Completed && checked) {
                return (
                  <ListItem
                    key={index}
                    id={index}
                    name={item}
                    checked={checked}
                    handleCheck={handleCheck}
                    deleteItem={deleteItem}
                  />
                )
              } else if (state.Active && !checked) {
                return (
                  <ListItem
                    key={index}
                    id={index}
                    name={item}
                    checked={checked}
                    handleCheck={handleCheck}
                    deleteItem={deleteItem}
                  />
                )
              }
            })}
            <li className="details">
              <Zoom in={true}>
                <div>
                  <span>{left}</span> Item
                  {/* fix the grammer problem */}
                  <span>{items.length > 1 ? "s" : ""}</span> left
                </div>
              </Zoom>
              <div className="sub-details">
                <div
                  style={{
                    color: state.All && "aqua",
                    transform: state.All && "scale(1.2)",
                    cursor: "pointer"
                  }}
                  onClick={handleState}
                >
                  All
                </div>
                <div
                  style={{
                    color: state.Active && "aqua",
                    transform: state.Active && "scale(1.2)",
                    cursor: "pointer"
                  }}
                  onClick={handleState}
                >
                  Active
                </div>
                <div
                  style={{
                    color: state.Completed && "aqua",
                    transform: state.Completed && "scale(1.1)",
                    cursor: "pointer"
                  }}
                  onClick={handleState}
                >
                  Completed
                </div>
              </div>
              <div style={{ cursor: "pointer" }} onClick={deleteCompleted}>
                Clear completed
              </div>
            </li>
          </ul>
        )}
      </div>
    </div>
  )
}
export default ListContainer
