import React, { useEffect, useState } from "react"
import CloseIcon from "@mui/icons-material/Close"
import { Fade } from "@mui/material"

import CheckIcon from "@mui/icons-material/Check"
import { Zoom } from "@mui/material"

function ListItem({ name, id, checked, handleCheck, deleteItem }) {
  let [check, setCheck] = useState(false)

  const [cross, setCross] = useState(false)
  const style = {
    color: "hsl(233, 14%, 35%)",
    fontSize: "1.7rem",
    position: "absolute",
    right: "1rem",
    cursor: "pointer"
  }

  function handleChange() {
    setCross(true)
  }
  function handleChangeTwo() {
    setCross(false)
  }
  useEffect(() => {
    setCheck(checked)
  }, [checked])
  return (
    <Zoom in={true}>
      <li onMouseEnter={handleChange} onMouseLeave={handleChangeTwo}>
        <div
          className="checkbox"
          onClick={() => {
            handleCheck(id)
          }}
          style={{
            background: checked
              ? "-webkit-linear-gradient(hsl(192, 100%, 67%), hsl(280, 87%, 65%))"
              : ""
          }}
        >
          {/* icon will be set here... */}
          {check && <CheckIcon style={{ color: "white" }} />}
        </div>
        <div
          style={{
            fontSize: "1.2rem",
            textDecoration: check ? "line-through" : "",
            color: check ? "hsl(234, 11%, 52%)" : ""
          }}
        >
          {name}
        </div>

        {/* delete icon */}
        {cross && (
          <Fade in={cross}>
            <CloseIcon
              onClick={() => {
                console.log("clicked....")
                deleteItem(id)
              }}
              style={style}
            />
          </Fade>
        )}
      </li>
    </Zoom>
  )
}

export default ListItem
